/* font */
body{
  font-family: 'Roboto Slab','Noto Sans KR', sans-serif;  
}
.scrollLock { overflow: hidden; }
/* common */

.clrfix{
  display: block;
  clear: both;
}
.circle{
  border-radius: 50%;
}
.round{
  border-radius: 10%;
}
/* animation */
@keyframes fadeUp {
  0%{
    opacity: 0;
    margin-top: 40px;
  }
  100%{
    opacity: 1;
    margin-top: 0px;
  }
}
@keyframes bounce {
  0%{    
    top: 0;
  }  
  50%{    
    top: -10px;
  }
  100%{
    top: 0;
  }
}
@keyframes neon {
  from{
    box-shadow: 0 0 3px 3px rgb(250,222,166);
  }
  50%{
    box-shadow: 0 0 3px 3px #FDB165;
  }
  to{
    box-shadow: 0 0 3px 3px rgb(245,236,218);
  }
}
.header .percent.off{
  display: none;
}
.header .percent{
  display: block;
  position: fixed;
  top: 60px;  
  height: 1px;
  background: #FFF;
  animation: neon 2s alternate infinite;
}
.content.off{
  transform: perspective(100px) rotateX(45deg) scale(0%);
  position: relative;
  top: 40px;
}
.content.on{
  transform: perspective(unset) rotateX(0deg) scale(100%);
  transition: all 1s;
  position: relative;
  top: 0px;
}

/* mobile */
@media screen and (max-width:1200px) {
  .container{
    width: 90%;
    height: 100%;
    margin: auto;  
    position: relative;
    overflow: hidden;
  }
  /* header */
.header{
  width: 100%;
  height: 60px;
  position: fixed;
  z-index: 1;
  color: #FFF;
}
.header.on{
  background-color: rgba(0, 0, 0, 0.6);
  transition : all 0.5s;
}
.header.off{
  background-color: none;
  transition : all 0.3s;
}
.header .container{
  display: flex;
  justify-content: space-between;
}
.header .headTitle{
  line-height: 60px;
  font-size: 1.2rem;
  font-weight: 500;
}
.header .container .headItems{
  width: 60%;
  display: flex;
  justify-content: space-between;
}
.header .container .headItems .headItem{
  line-height: 60px;
  font-size: 0.9rem;
  cursor: pointer;
}
/* Page common */
.Page{
  width: 100%;
  position: relative;
  padding: 80px 0px 80px 0px;
}
.Page h1{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  font-size: 2rem;   
  color: #FFF;
  line-height: 80px;  
  animation: fadeUp 1s;
  margin: 0;
}
.Page h2{
  text-align: center;
  font-size: 1.5rem;
}
.Page h3{
  text-align: center;
  font-size: 1.2rem;
}
/* page home */
.Page.home{    
  height: calc(100vh - 160px);
  background: url('./../public/home_bg.jpg')0px 0px;
  background-size: cover;
}
/* page about */
.Page.about{
  background: #F6F6F2;  
}
.Page.about .content.on{
  opacity: 1;
  transition: all 0.5s;
}
.Page.about .content.off{
  opacity: 0;
  transition: all 0.5s;
}
.Page.about .infoItem .info{
  float: left;
  width: 30%;
  text-align: center;  
  background: #FFF;
  margin: 4px;
  border-radius: 10px;
  box-shadow: 0px 0px 4px #000;
  margin-bottom: 20px;
}
.Page.about .infoItem .info.mr{
  margin-right: calc(5% - 8px);
}
.Page.about .infoItem .info p{  
  word-break: break-all;
}
/* page Intro */
.Page.skills{
  background: #BADFE7;
}
.Page.skills .content.on{
  opacity: 1;
  transition: all 0.5s
}
.Page.skills .content.off{
  opacity: 0;
  transition: all 0.5s
}
.Page.skills h3{
  text-align: center;
  padding-bottom: 20px;
}
.Page.skills ul{
  width: 100%;  
  display: block;
  padding: 20px 0px;
}
.Page.skills ul li{
  list-style: none;
  text-align: center;
  padding-bottom: 4px;
  font-size: 1.1rem;
  font-weight: 500;  
  padding: 8px;
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0px 1px 4px #333;  
  text-shadow: 0px 0px 3px #CCC;
  box-sizing: border-box;  
  float: left;
  margin: 4px;  
}
.Page.skills ul li.clrfix{
  padding: 0px;

  display: block;
  clear: both;
}
.Page.skills .portIntro .list h4{
  font-size: 1.4rem;
  font-weight: 700;  
}
/* Page portfolio */
.Page.portfolio{
  
}

.Page.portfolio .content.on{
  opacity: 1;
  transition: all 0.5s;  
}
.Page.portfolio .content.off{
  opacity: 0;  
  
}

.Page.portfolio .box{
  width: 100%;  
  height: 100%;
  cursor:pointer;
  position: relative;
}
.Page.portfolio .box a{
  display: block;
  position: relative;
  width: 100%;
}
.Page.portfolio .box a:hover::before{
  content:"";
  display: block;
  width: 100%;
  height: 100%;  
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  box-sizing: border-box;
  position: absolute;
  opacity: 1;  
  background: rgba(0, 0, 0, 0.9);
}
.Page.portfolio .box a:hover::after{
  content: "자세히 보기";
  font-size: 2rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  text-decoration: none;
  color: #FFF;
  margin: 0;
  transition: all 0.5s
}
.Page.portfolio .box img{
  width: 100%;
}
.Page.portfolio .box p.explain{
  
}
.Page.portfolio.p_p_1{
  background: #6FB3B8;
}
.Page.portfolio.p_p_2{
  background: #6FB3B8;
}
.Page.portfolio.p_p_3{
  background: #6FB3B8;
}

/*modal*/
.modal{
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  z-index: 444;
  top: 0;
  left: 0;
  touch-action:none;
  padding: 60px 0px;
  box-sizing: border-box;  
}
.modal .box{
  border-radius: 20px;
  background: #FFF;
  position: relative;
  padding: 30px;
  box-sizing: border-box;
}
.modal .box .closebtn{
  display: block;
  position: absolute;
  right: 15px;
  top: 15px;
  background: #328;
  font-size: 1.4rem;
  padding: 12px;
  text-align: center;
  color: #FFF;
  text-decoration: none;
}
.modal h2{
  text-align: center;
  display: block;
  margin: 0;
  font-size: 1.4rem;
  padding: 0px 0px;
}
.modal h3{
  text-align: center;
  display: block;
  margin: 0;
  font-size: 1.2rem;
  padding: 10px 0px 10px;
}
.modal p{
  font-size: 1rem;
}
.modal .modal_link{
  display: block;
  padding: 12px;
  background: #492;
  position: absolute;
  color: #FFF;
  text-decoration: none;
  font-size: 1.4rem;
  right: 15px;
  bottom: 15px;
  border-radius: 10px;
  transition: all 0.5s;
}
.modal .modal_link:hover{
  box-shadow: 0px 0px 4px #000;
  transition: all 0.5s;
}
/*footer*/
.Page.footer{
  background: #388087;
  color: #FFF;
  width: 100%;
  padding: 40px 0;
  text-align: center;
}
}
